/* Global SCSS partial */
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", "Inter", sans-serif;
}

h1, h2, h3 {
  font-weight: 500;
  line-height: 1.1;
  margin-top: 0;
}

p {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
  color: var(--dark-grey-color);
}

a:hover {
  color: var(--mid-grey-color);
}

:root {
  --dark-grey-color: #303133;
  --mid-grey-color: #505676;
  ---mid-light-grey-color: #BDBEC2;
  --light-grey-color: #F4F5F7;
  --white-color: #fff;
  /* Stage colors */
  --proposal-color: #047243;
  --lead-color: #007BB7;
  --negotiation-color: #6B43C8;
  --won-color: #04B767;
  /* Priority colors */
  --high-color: #FD582A;
  --low-color: #5CC5FF;
}

.nav__main__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5em 5em 0 5em;
}

.nav__main__menu-item {
  list-style-type: none;
  display: flex;
}

.nav__main__menu-links {
  padding: 0 1em 0 1em;
  display: block;
}